<template>
  <header class="header">
    <div class="header__body">
      <div class="container">
        <div class="row">
          <div class="header__logo col-3">
            <a href="#">
              <img src="../assets/style/image/logos/logo.svg" alt="" />
            </a>
          </div>

          <div @click="burgerVisible" class="header__burger-menu ml-auto">
            <img ref="burger__img" src="../assets/style/image/logos/menu icon.svg" alt="burger-logo" />
          </div>

          <nav
            @click="burgerVisible"
            class="header__menu ml-auto"
            ref="header__menu"
            v-bind:class="{ asideMenu: isActive }"
          >
            <ul class="menu__list d-flex justify-content-between">
              <li @click="itemVisible" class="menu__item">
                <a href="#about-me">About me</a>
                <a href="#">Relationship</a>
                <a href="#">Requirements</a>
                <a href="#users">Users</a>
                <a href="#register">Sing Up</a>

                <div
                  v-if="isAdditionalItemVis"
                  class="list__additional d-flex flex-column"
                >
                  <a href="#">How it works</a>
                  <a href="#">Partnership</a>
                  <a href="#">Help</a>
                  <a href="#">Leave testimonial</a>
                  <a href="#">Contack us</a>
                </div>

                <div
                  v-if="isAdditionalItemVis"
                  class="list__additional d-flex flex-column"
                >
                  <a href="#">Article</a>
                  <a href="#">Our news</a>
                  <a href="#">Testimonials</a>
                  <a href="#">Lisenses</a>
                  <a href="#r">Privacy Police </a>
                </div>
              </li>
            </ul>
          </nav>
        </div>
      </div>
    </div>
  </header>
</template>

<script>
export default {
  name: "vHeader",
  data() {
    return {
      isAdditionalItemVis: false,
      isActive: false,
    };
  },
  computed: {},
  methods: {
    AdditionalItemVis() {
      if (document.documentElement.clientWidth < 1024) {
        this.isAdditionalItemVis = !this.isAdditionalItemVis;
      }
    },
    itemVisible() {
      if (document.documentElement.clientWidth < 1024) {
        this.isActive = !this.isActive;
        if (this.isActive == true) {
          document.body.style.overflow = "hidden";
        } else if (this.isActive == false) {
          document.body.style.overflow = "auto";
        }
      }
    },
    burgerVisible(event) {
      let burger = this.$refs.burger__img;
      let headerMenu = this.$refs.header__menu;

      if (event.target == burger || event.target == headerMenu) {
        this.itemVisible();
      }
    },
  },
  mounted() {
    this.AdditionalItemVis();
  },
};
</script>
