<template>
  <div  @click="popUpVisible" class="popUp d-flex">
    <div class="popUp__body align-self-center d-flex flex-column">
      <div class="popUp__tittle d-flex">
        Congratulations

        <i class="fas fa-times ml-auto"></i>
      </div>

      <div class="popUp__desc">
        You have successfullly passed the registration
      </div>

      <div @click="great" class="popUp__button align-self-end">
        <button class="button">Great</button>
      </div>
    </div>
  </div>
</template>

<script>
import { mapActions } from 'vuex';

export default {
  name: "vPopUp",

  data() {
    return {
      
    };
  },
  methods: {
    ...mapActions(["POP_UP_VISIBLE"]),
     great(){
       this.POP_UP_VISIBLE();
       location.reload()
     },
     popUpVisible(event) {
      let popup = document.querySelector(".popUp");
      let close = document.querySelector(".fa-times");
      if (event.target == popup || event.target == close  ) {
        this.POP_UP_VISIBLE();
        location.reload()
      }
    },
  },
};
</script>