<template>
  <section id="register" class="register">
    <vPopUp v-if="GET_POP_UP_VISIBLE" />

    <div class="container">
      <div class="row d-flex flex-column">
        <div class="register__header block-header col-sm-8 col-lg-7">
          Register to get a work
        </div>
        <div class="register__attantion col-md-8 col-lg-6">
          Attention! After successful registration and alert, update the list of
          users in the block from the top
        </div>

        <div class="form col-md-8 col-lg-6 align-self-center">
          <form @submit.prevent="submit">


            <div
              class="form__item"
              :class="{ inputError: $v.form.name.$error }"
            >
              <label for="formName" class="form__label">Name</label>
              <input
                id="formName"
                class="form__input"
                placeholder="Your name"
                :class="{ error: $v.form.name.$error }"
                v-model.trim="$v.form.name.$model"
              />
            </div>

            <div
              class="form__item"
              :class="{ inputError: $v.form.email.$error }"
            >
              <label for="formEmail" class="form__label">Email</label>
              <input
                @change="change_email"
                id="formEmail"
                type="text"
                class="form__input"
                placeholder="Your email"
                :class="{ error: $v.form.email.$error }"
                v-model.trim="$v.form.email.$model"
              />
            </div>

            <div
              class="form__item"
              :class="{ inputError: $v.form.phone.$error }"
              
            >
              <label for="formNumber" class="form__label">Phone number</label>
              <input
                
                type="tel"
                v-mask="'+380#########'"
                id="formNumber"
                class="form__input"
                placeholder="+380 XX XXX XXXX"
                :class="{ error: $v.form.phone.$error }"
                v-model.trim="$v.form.phone.$model"
                
                ref="userPhone"
                
              />
            </div>

            <div class="form__item">
              <div class="form__tittle">Select your position</div>

              <div class="form__options">
                <div
                  class="options__item"
                  v-for="(position, index) in positions"
                  :key="index"
                >
                  <input
                    :id="position.id"
                    type="radio"
                    :value="position.id"
                    name="position"
                    class="options__input"
                    v-model="form.positionId"
                  />
                  <label :for="position.id" class="options__label">
                    {{ position.label }}
                  </label>
                </div>
              </div>
            </div>

            <div class="form__item">
            <div class="form__label form__photo">Photo</div>

              <div class="form__file">
                <div
                  class="file__item d-flex"
                  :class="{ error: $v.form.file.$error }"
                >
                  <input
                    type="file"
                    name="file"
                    id="file"
                    class="file"
                    ref="file"
                    :v-model="form.file"
                    accept="image/jpeg,image/png,image/jpg"
                    v-on:change="handleFileUpload()"
                  />
                  <label
                    for="file"
                    class="file__input flex-nowrap"
                    :class="{ error: $v.form.file.$error }"
                    >
                    <span>
                      {{ fileVariant }}
                    </span>
                  </label>
                  <label
                    for="file"
                    class="file__browse"
                    :class="{ error: $v.form.file.$error }"
                  >
                  <span>
                    Browse
                  </span>
                    
                  </label>
                </div>
              </div>
            </div>

            <div class="form__item form__button">
              <button
                class="button"
                type="submit"
                :disabled="form.submitStatus === 'PENDING'"
              >
                Sing up now
              </button>
            </div>
          </form>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import { mapActions , mapGetters } from 'vuex';

import axios from "axios";
import Inputmask from "inputmask";
import vPopUp from "./v-pop-up"

import {
  required,
  minLength,
  maxLength,
  between,
  email,
  checked,
} from "vuelidate/lib/validators";

export default {
  name: "vRegisterForm",
  components: {
    vPopUp 
  },

  data() {
    return {
      fileVariant: "Upload your photo",
      isPhotoUpload: false,
      form: {
        name: "",
        email: "",
        phone: "",
        positionId: "1",
        file: null,
        submitStatus: null,
      },
      positions: [
        {
          label: "Frontend Developer",
          id: "1",
        },
        {
          label: "Backend Developer",
          id: "2",
        },
        {
          label: "Designer",
          id: "3",
        },
        {
          label: "QA",
          id: "4",
        },
      ],
    };
  },

  validations: {
    form: {
      name: {
        required,
        minLength: minLength(2),
        maxLength: maxLength(60),
      },
      email: {
        required,
        email,
        minLength: minLength(2),
        maxLength: maxLength(100),
      },
      phone: {
        required,
        between: between(+380000000000, +380999999999),
      },
      file: {
        required
      },
    },
  },
  computed: {
    ...mapGetters(["GET_POP_UP_VISIBLE"]),
  },
  methods: {
    ...mapActions(["POP_UP_VISIBLE"]),
    
    change_email(){
      this.form.email = this.form.email.toLowerCase();
    },
    handleFileUpload() {
      if(this.$refs.file.files[0] != undefined) {
        this.form.file = this.$refs.file.files[0];
      }
      if (this.form.file == undefined) {
        if(!this.isPhotoUpload ) {
        this.fileVariant = "No file chosen";
        }
      }
      else {
        this.isPhotoUpload = true;
        this.fileVariant = this.form.file.name;
      }
    },
    submit() {
      let vm =this;
      this.$v.form.$touch();
      if (this.$v.form.$invalid) {
        if (this.form.file == null) {
          this.fileVariant = "No file chosen";
        }
        
        this.form.submitStatus = "ERROR";
      } else {
        this.form.submitStatus = "PENDING";
        setTimeout(() => {
          this.form.submitStatus = "OK";
          vm.submitFile();
        }, 500);
      }
    },
    submitFile() {
      let vm = this;
      let user = {
        name: vm.form.name,
        email: vm.form.email,
        phone: vm.form.phone,
        positionId: vm.form.positionId,
        file: vm.form.file,
      };
      axios
        .get("https://frontend-test-assignment-api.abz.agency/api/v1/token")
        .then((responce) => {
          vm.token = responce.data.token;

          var bodyFormData = new FormData();
          bodyFormData.append("name", vm.form.name);
          bodyFormData.append("email", vm.form.email);
          bodyFormData.append("phone",  vm.form.phone);
          bodyFormData.append("position_id",  vm.form.positionId);
          bodyFormData.append("photo", vm.form.file);

          axios
            .post(
              "https://frontend-test-assignment-api.abz.agency/api/v1/users",
              bodyFormData,
              {
                headers: {
                  Token: responce.data.token,
                  "Content-Type": "multipart/form-data",
                },
              }
            )

            .then(function () {
              vm.popUpVisible();
            })
            .catch(function () {
              alert("FAILURE!!");
            });
        });
    },
    popUpVisible(){
      this.POP_UP_VISIBLE()
    }

  },

  watch: {
  },

  mounted() {
  },
};
</script>