import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    isPopUpVisible: false,
    userArray: [],
    baseMyUrl: 'https://frontend-test-assignment-api.abz.agency/api/v1',
    userCount: 6,
    userPage: 1,
    totalUsers: 0,
  },
  mutations: {
    SET_USERS_TO_STATE: (state, responce) => {
      state.userArray = responce.users;
    },
    GET_MORE_USERS:(state ) =>{
      // console.log('user count' + state.userCount)
      // console.log('array length' + state.userArray.length)
      state.userCount += 6
    },
    SET_POP_UP_VISIBLE:(state) =>{
      state.isPopUpVisible = !state.isPopUpVisible

      if (state.isPopUpVisible == true) {
        document.body.style.overflow = "hidden"
    } else if (state.isPopUpVisible == false) {
        document.body.style.overflow = "auto"
    }
    }
  },
  actions: {
    GET_USERS_FROM_API({commit , state}) {
      return axios(state.baseMyUrl + '/users', {
          method: "GET",
          params:{
            count: state.userCount,
            page : state.userPage
          }
        })
        .then((responce) => {
          commit('SET_USERS_TO_STATE', responce.data)
          state.totalUsers = responce.data.total_users
          return responce;
        })
        .catch((error) => {
          console.log(error);
          return error
        })
    },
    SHOW_MORE_USERS({commit} ){
      commit('GET_MORE_USERS')
    },
    POP_UP_VISIBLE({commit}){
      commit('SET_POP_UP_VISIBLE')
    }

  },
  getters: {
    GET_USERS(state) {
      return state.userArray
    },
    GET_USER_COUNT(state) {
      console.log(state.userCount)
      
      return state.userCount
    },
    GET_TOTAL_USERS(state) {
      console.log(state.totalUsers)
      return state.totalUsers
    },
    GET_POP_UP_VISIBLE(state){
      return state.isPopUpVisible
    }
  }
})