<template>
  <div class="user col-12 col-sm-6 col-md-6 col-lg-4">
    <!-- <div class="user__photo">
      <img :src="users.photo" alt="user-photo" />
    </div> -->
    <div class="user__photo">
      <img :src="this.myUrl" alt="user-photo" />
    </div>
    <div :title="users.name" class="user__name">{{ users.name }}</div>
    <div class="user__info">
      <p class="user__position">{{ users.position }}</p>
      <p :title="users.email" class="user__email">{{ users.email }}</p>
      <p class="user__phone">{{ users.phone }}</p>
    </div>
  </div>
</template>

<script>
export default {
  name: "vUserData",
  data() {
    return {
      myUrl: "",
      myPhoto: require("../assets/style/image/logos/photo-cover.svg"),
    };
  },
  props: {
    users: {
      type: Object,
      default() {
        return {};
      },
    },
  },
  computed: {
  },
  methods: {
    getPhoto() {
      let vm = this;
      var tester = new Image();
      tester.onload = imageFound;
      tester.onerror = imageNotFound;
      tester.src = this.users.photo;

      function imageFound() {
        return (vm.myUrl = vm.users.photo);
      }

      function imageNotFound() {
        return (vm.myUrl = vm.myPhoto);
      }
    },
  },

  mounted() {
    this.getPhoto();
  },
};
</script>