<template>
  <div id="users" class="users">
    <div class="container">
      <div class="row d-flex flex-column">
        <div class="users__header block-header col-12 col-md-6">
          Our cheerful users
        </div>
        <div class="users__sort col-12 col-md-6">
          Attention! Sorting users by registration date
        </div>
        <div class="users__block col-12">
          <div class="row">
            <vUserData v-for="item in GET_USERS" :key="item.id" :users="item" />
          </div>
        </div>
        <div class="users__button">
          <button
            @click="showMoreUsers"
            class="button"
            v-show="isShowMoreButtonVis"
          >
            Show more
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
import vUserData from "../components/v-user-data";
export default {
  name: "vUsers",
  components: {
    vUserData,
  },
  data() {
    return {
      isShowMoreButtonVis: true,
    };
  },
  computed: {
    ...mapGetters(["GET_USERS"]),
    ...mapGetters(["GET_TOTAL_USERS"]),
    ...mapGetters(["GET_USER_COUNT"]),
  },
  methods: {
    ...mapActions(["GET_USERS_FROM_API"]),
    ...mapActions(["SHOW_MORE_USERS"]),

    showMoreUsers() {
      let vm = this;
      if (this.GET_USER_COUNT >= this.GET_TOTAL_USERS-6) {
        vm.isShowMoreButtonVis = false;
      }
      this.SHOW_MORE_USERS();
      this.GET_USERS_FROM_API();
    },
  },

  mounted() {
    this.GET_USERS_FROM_API().then((responce) => {
    });
  },
};
</script>